.rates {
  padding-bottom: 50px;

  &__heading {
    margin-bottom: 12px;
  }

  &__intro {
    margin: 0;
    margin-bottom: 20px;

    font-size: 20px;
    line-height: 28px;
  }

  &__list, &__items {
    margin-bottom: 30px;
  }

  &__item {
    margin-bottom: 16px;
    padding: 25px 20px 30px 20px;

    border-radius: 10px;

    &--express {
      background: linear-gradient(180deg, #b5ffa3 0%, rgba(255, 255, 255, 0) 100%), #cafba4;
    }

    &--standart {
      border: 1px solid #d8d8d8;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;

    font-size: 24px;
    line-height: 34px;
    font-weight: 800;
  }

  &__desc {
    margin: 0;

    font-size: 20px;
    line-height: 28px;
  }

  @media (min-width: 768px) {
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: baseline;
      flex-wrap: wrap;
    }

    &__intro {
      order: 2;
      width: 48%;
      margin-bottom: 0;
    }

    &__heading {
      width: 100%;
      margin-bottom: 14px;
    }

    &__row {
      display: flex;
      flex-wrap: wrap;
    }

    &__items {
      order: 1;
      width: 47%;
      margin-bottom: 0;
    }

    &__list {
      display: flex;
      align-items: baseline;
      order: 2;
      width: 100%;
      margin-bottom: 19px;
    }

    &__item {
      width: 48.5%;
      margin-bottom: 0;

      &--express {
        margin-right: 20px;
      }
    }

    &__desc {
      font-size: 18px;
      line-height: 26px;
    }

    &__payments {
      order: 3;
      width: 407px;
    }

    &__row {
      margin-bottom: 30px;
    }

    @media (min-width: 1170px) {
      & {
        padding-bottom: 70px;
      }

      &__row {
        margin-bottom: 40px;
      }

      &__intro {
        width: 469px;

        font-size: 25px;
        line-height: 35px;
      }

      &__items {
        width: 525px;
        margin-right: 46px;

        font-size: 25px;
        line-height: 35px;
      }

      &__list {
        margin-bottom: 5px;
      }

      &__item {
        width: 457px;
      }

      &__title {
        font-size: 25px;
        line-height: 35px;
      }

      &__desc {
        font-size: 25px;
        line-height: 35px;
      }
    }
  }
}
