.contacts {
  padding-top: 40px;
  padding-bottom: 50px;

  font-size: 20px;

  &__heading {
    margin-bottom: 16px;
  }

  &__subheading {
    margin-bottom: 12px;
  }

  &__links {
    margin: 0;
    margin-bottom: 10px;

    & a {
      font-size: 24px;
      line-height: 34px;
      color: #000000;
    }
  }

  &__work-hours {
    margin: 0;
    margin-bottom: 16px;
    padding-bottom: 16px;

    border-bottom: 1px solid #cdcdcd;
  }

  &__chat {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0;
    margin-bottom: 20px;
  }

  &__chat-link {
    display: inline-block;
    width: 177px;
    height: 50px;
    background-color: #25d366;
    background-image: url("../images/logo-whatsapp.svg");
    background-repeat: no-repeat;
    background-position: center;
  }

  &__taxi-call {
    margin: 0;
    position: relative;
    margin-bottom: 25px;
    padding: 60px 15px 30px;

    line-height: 28px;

    border-radius: 4px;
    background-color: #ffeB81;

    &:before {
      content: '';
      position: absolute;
      top: 20px;
      left: 15px;
      width: 62px;
      height: 25px;
      background-image: url("../images/icon-taxi.svg");
      background-repeat: no-repeat;
    }
  }

  &__map {
    display: block;
    width: 100%;
    margin-bottom: 30px;
  }

  @media (min-width: 768px) {
    &__chat {
      font-size: 20px;
      line-height: 28px;
    }

    &__taxi-call {
      padding: 22px 25px 22px 132px;

      &:before {
        top: 50%;
        left: 25px;
        transform: translate(0,-50%);
      }
    }

    &__row {
      display: flex;
      justify-content: space-between;
    }

    &__column {
      width: 49.3%;
    }

    &__chat-link {
      width: 135px;
    }
  }

  @media (min-width: 1170px) {
    &__inner {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }

    &__heading {
      width: 100%;
    }

    &__row {
      justify-content: start;
      flex-direction: column;
      order: 1;
      width: 384px;
    }

    &__column {
      width: auto;
    }

    &__taxi-call {
      order: 3;
      width: 100%;
      margin-top: 38px;
    }

    &__map {
      order: 2;
      width: 621px;
      margin-top: -50px;
      margin-bottom: 0;
    }

    &__subheading {
      order: 4;
      margin-bottom: 20px;
    }

    &__tabs {
      order: 5;
    }

    &__chat-link {
      width: 177px;
    }
  }
}
