@keyframes bounce {
  0% { transform: translateX(100%); }
  100% { transform: translateX(0); }
}

.quiz {

  &__block {
    display: block;
    color: #ffffff;
    background-color: #2B2C30;
  }

  &--hide {
    display: none;
  }

  &__inner {
    padding-top: 60px;
  }

  &__counter {
    position: relative;
    margin: 0;
    margin-bottom: 35px;
    padding-bottom: 16px;

    &:before {
      content: '';
      left: 0;
      bottom: 0;
      position: absolute;
      width: 100%;
      height: 3px;
      background-color: #ffffff;
    }

    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0px;
      height: 3px;
      background-color: #2BBA53;
    }

    &--step1:after {
      width: 14%;
    }

    &--step2:after {
      width: 28%;
    }

    &--step3:after {
      width: 42%;
    }

    &--step4:after {
      width: 56%;
    }

    &--step5:after {
      width: 70%;
    }

    &--step6:after {
      width: 84%;
    }
    &--step7:after {
      width: 95%;
    }
  }

  &__step {
    opacity: 0;
    height: 0;
  }

  &__step--active {
    opacity: 100;
    height: 100%;
    animation-name: bounce;
    animation-duration: 0.4s;
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    font-size: 25px;
    line-height: 35px;
    font-weight: 700;
  }

  &__desc {
    margin: 0;
    margin-bottom: 30px;
    font-size: 20px;
    line-height: 29px;
  }

  &__sum {
    margin: 0;
    margin-bottom: 10px;
    font-size: 40px;
    line-height: 56px;
    font-weight: 500;
  }

  &__cost {
    display: block;
    margin-top: -15px;
    margin-bottom: 30px;
    font-size: 16px;
  }

  &__bonus {
    padding: 20px 15px;
    margin-bottom: 35px;
    border: 1px solid #FFEB81;
    border-radius: 5px;

    & b {
      display: block;
    }
  }

  &__form ul {
    margin: 0;
    margin-bottom: 30px;
    padding: 0;
    list-style: none;
  }

  &__form li {
    margin-bottom: 5px;
  }

  &__options {
    margin: 0;
    margin-bottom: 50px;
  }

  &__option {
    display: block;
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__label {
    position: relative;
    display: inline-block;
    width: 100%;
    margin: 0;
    padding: 15px 50px 10px 50px;
    -webkit-appearance: none;
    -moz-appearance: none;
    background: #ffffff;
    outline: none;
    transition: 0.2s;
    position: relative;
    color: #000000;
    text-align: center;
    border-radius: 2px;
    font-weight: 500;
    border: 2px solid #ffffff;

    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 15px;
      width: 24px;
      height: 24px;
      border: 2px solid #D3D3D5;
      border-radius: 50%;
      transform: translate(0,-50%);
    }

    @media(min-width: 1170px) {
      &:hover {
        background-color: #DDFFE2;
        border-color: #DDFFE2;
      }
    }
  }

  &__label--checkbox:before {
    border-radius: 0;
  }

  &__check {
    display: none;
  }

  &__option &__check:checked + &__label {
    background-color: #DDFFE2;
    border: 2px solid #2BBA53;

    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 15px;
      width: 24px;
      height: 24px;
      padding: 5px;
      background-image: radial-gradient(#2BBA53 45%, #DDFFE2 50%);
      border: 3px solid #2BBA53;
      border-radius: 50%;
      transform: translate(0,-50%);
    }
  }

  &__option &__check:checked + &__label--checkbox:after {
    background-image: url("../images/check.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-color: #2BBA53;
    border-radius: 0;
  }

  &__btns {
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
    align-items: center;
    margin: 0;
  }

  &__btn {
    position: relative;
    padding: 10px;
    color: #ffffff;
    background-color: transparent;

    &--next {
      padding: 13px 45px 11px 27px;
      background-color: #1FA2FA;
      border-radius: 2px;

      &:hover {
        background-color: #0085DF;
      }

      &:disabled {
        background-color: #BABABA;
      }
    }

    &--next:after {
      content: '';
      position: absolute;
      top: 15px;
      right: 25px;
      width: 10px;
      height: 16px;
      background-image: url("../images/arrow-white.svg");
      background-repeat: no-repeat;
    }

    &__prev {
      color: #ffffff;
    }
  }

  &__privacy {
    margin-bottom: 65px;
    line-height: 26px;
  }

  &__close {
    top: 0;
  }
}

@media (min-height: 850px) {
  .quiz__submit-btn {
    position: absolute;
    bottom: 50px;
    width: 93%;
  }
}

input[type="number"] {
  border: 2px solid #1FA2FA;
}

input[type="number"]:focus {
  border: 2px solid #1FA2FA;
  outline: none;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
