.page-footer {
  padding-bottom: 100px;

  &__copyright {
    margin: 0;
    margin-bottom: 10px;

    color: #a6a6a6;
  }

  &__privacy {
    padding-bottom: 2px;

    color: #a6a6a6;

    border-bottom: 1px solid rgba(166, 166, 166, 0.8);
  }

  @media (min-width: 768px) {
    & {
      padding-bottom: 25px;
    }
  }

  @media (min-width: 1170px) {
    &__inner {
      display: flex;
      align-items: flex-start;
    }

    &__copyright {
      margin-right: 60px;
    }
  }
}
